@import url(https://fonts.googleapis.com/css2?family=Poppins:wght@100&display=swap);
* {
	margin: 0;
	padding: 0;
	box-sizing: border-box;
}

body {
	margin: 0;
}

.App {
	font-family: 'Poppins', sans-serif;
	font-size: 0.9em;
	font-weight: 400;
}

.content {
	margin-top: 50px;
	width: 100%;
	min-height: 60vh;
	display: -webkit-flex;
	display: flex;
	-webkit-justify-content: center;
	        justify-content: center;
	-webkit-flex-direction: column;
	        flex-direction: column;
	-webkit-align-items: center;
	        align-items: center;
	background-color: #400199;
	color: #f8f3fc;
	padding: 1em;
	text-align: center;
}

h3 {
	margin-bottom: 1.5em;
	margin-top: 1em;
}

.btn {
	font-size: 1em;
	color: #fff;
	background: #34b6b4;
	display: inline-block;
	padding: 10px 20px;
	margin-top: 25px;
	text-decoration: none;
	letter-spacing: 2px;
	transition: 0.5s;
	border: none;
	border-radius: 4px;
	outline: none;
}

.content input[type='text'] {
	width: 31em;
	outline: none;
	padding: 8px;
	height: 2.8em;
	margin: 0.25em;
	border: none;
	font-size: 1em;
}
.btn:hover {
	letter-spacing: 6px;
}

header {
	background-color: #400199;
	position: fixed;
	top: 0px;
	left: 0;
	width: 100%;
	height: 50px;
	padding: 35px 50px;
	z-index: 10000;
	display: -webkit-flex;
	display: flex;
	-webkit-justify-content: space-between;
	        justify-content: space-between;
	-webkit-align-items: center;
	        align-items: center;
	transition: 0.5%;
}

header .logo {
	color: #fff;
	font-weight: 700;
	font-size: 2em;
	text-decoration: none;
	margin-right: 200px;
}

header .logo h2 {
	color: #34b6b4;
	font-weight: 700;
}
header .navigation {
	display: -webkit-flex;
	display: flex;
}
header .navigation li {
	list-style: none;
	margin-right: 30px;
	color: #fff;
	font-weight: 300;
}
.navigation span {
	color: #34b6b4;
	font-weight: 500;
	text-transform: uppercase;
}
.navigation Link {
	text-decoration: none;
}
li {
	cursor: pointer;
}
li i {
	color: #d8dae6;
	margin-left: 0.5em;
}

.job {
	margin-top: 50px;
	padding: 20px 50px 50px 50px;
}
.job-description {
	margin-top: 3em;
	margin-bottom: 2em;
}
.job-description p {
	color: #34b6b4;
}

h4 {
	color: blueviolet;
	margin-top: 1em;
}

.description {
	white-space: pre-wrap;
}

.jobList {
	text-decoration: none;
	margin-top: 50px;
	padding: 3em;
	display: -webkit-flex;
	display: flex;
	-webkit-flex-direction: column;
	        flex-direction: column;
	-webkit-justify-content: center;
	        justify-content: center;
	width: 100%;
	-webkit-align-items: center;
	        align-items: center;
}

.jobList h3 {
	color: #34b6b4;
	text-decoration: none;
}
.jobList .singleJob {
	padding: 1em;
	margin-top: 0.5em;
	margin-bottom: 0.5em;

	box-shadow: 1px 5px 5px 4px #f3f3f3;
	border-radius: 5px;
	width: 60vw;
}

.form-container {
	display: -webkit-flex;
	display: flex;
	-webkit-flex-direction: column;
	        flex-direction: column;
	margin-top: 50px;
	padding: 70px;
	-webkit-justify-content: center;
	        justify-content: center;
	-webkit-align-items: center;
	        align-items: center;
}

.form {
	display: -webkit-flex;
	display: flex;
	-webkit-flex-direction: column;
	        flex-direction: column;
	width: 55%;
	font-family: Poppins;
}
.form span {
	color: mediumvioletred;
}
.form input {
	height: 2.9em;
	margin-top: 0.5em;
	margin-bottom: 0.5em;
	padding: 15px;
	font-family: Poppins;
}

.form h2 {
	color: #34b6b4;
}
#submit {
	background-color: #34b6b4;
	color: white;
	border: none;
}

.form textarea {
	resize: none;
	padding: 15px 15px;
	font-family: Poppins;
	border-radius: 5px;
	border: 1px solid #cccccc;
	overflow: hidden;
}

.mainDiv {
	display: -webkit-flex;
	display: flex;
	-webkit-flex-direction: column;
	        flex-direction: column;
	margin-top: 50px;
	padding: 100px;
	-webkit-justify-content: center;
	        justify-content: center;
	-webkit-align-items: center;
	        align-items: center;
}

.profile {
	padding: 3em;
	box-shadow: 1px 5px 7px 5px #f3f3f3;
	border-radius: 10px;
	width: 40vw;
}

.actionDiv {
	margin: 1em;
}
.actionDiv p {
	color: grey;
}
.actionDiv i {
	color: #34b6b4;
	margin: 0.6em;
}

.main_div {
	display: -webkit-flex;
	display: flex;
	margin-top: 50px;
	padding: 100px;
	-webkit-justify-content: center;
	        justify-content: center;
}
.cv {
	width: 70vw;
	padding: 3em;
	box-shadow: 1px 5px 7px 5px #f3f3f3;
	border-radius: 10px;
}

span {
	color: #34b6b4;
}
.aboutMe {
	white-space: pre-wrap;
	margin-top: 0.9em;
	margin-bottom: 0.9em;
	border-top: solid 0.5px darkgrey;
	padding-top: 0.9em;
}

